.logo {
	width: 100px;
	height: auto;
}

.form-icon {
	position: absolute;
	width: auto;
	bottom: 10px;
	right: 10px;
	margin-top: 0;
}

.signin-user-icon {
	width: 1em;
	height: 1em;
}

.signin-user-icon path,
.signin-user-icon polygon,
.signin-user-icon rect {
	fill: #222222;
}

.signin-user-icon circle {
	stroke: #222222;
	stroke-width: 1;
}

.signin-lock-icon {
	width: 1em;
	height: 1em;
}

.signin-lock-icon path,
.signin-lock-icon polygon,
.signin-lock-icon rect {
	fill: #222222;
}

.signin-lock-icon circle {
	stroke: #222222;
	stroke-width: 1;
}
